import React from "react";
import s from './Nav.module.scss'
import {Link} from "react-router-dom";


function Nav() {
    return (
        <div className={s.nav}>
            <Link to={'/'}></Link>
            <Link to={'/skills'}></Link>
            <Link to={'/works'}></Link>
            <Link to={'/contacts'}></Link>
        </div>
    );
}

export default Nav;